/* Tiles */

@mixin flex-align($horizontal, $vertical) {
  @include vendor('align-items', $horizontal);
  @include vendor('justify-content', $vertical);
}

$tiles-border-radius: 100%;
$tiles-trans-duration: 0.5s;
$tiles-ease: 'ease';

.tiles {
  $gutter: _size(gutter);

  @include vendor('flex-wrap', 'wrap');
  position: relative;
  margin: ($gutter * -1) 0 ($gutter * 1.5) ($gutter * -1);

  article {
    @include vendor('transition', (
            'transform #{$tiles-trans-duration} #{$tiles-ease}',
            'opacity #{$tiles-trans-duration} #{$tiles-ease}'
    ));
    position: relative;
    margin: ($gutter * 0.5) 0 0 ($gutter * 0.5);

    > .icon {
      @include vendor('transition', (
              'max-height #{$tiles-trans-duration} #{$tiles-ease}',
              'opacity #{$tiles-trans-duration} #{$tiles-ease}'
      ));
      width: 100%;
      max-height: 0;
      line-height: 1.5;
      margin-top: 0.35em;
      opacity: 0;

      > :last-child {
        margin-bottom: 0;
      }
    }

     > a, > div {
      @include vendor('display', 'flex');
      @include vendor('flex-direction', 'column');
      @include vendor('align-items', 'center');
      @include vendor('justify-content', 'center');
      @include vendor('transition', (
              'background-color #{$tiles-trans-duration} #{$tiles-ease}',
              'transform #{$tiles-trans-duration} #{$tiles-ease}'
      ));
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 1em;
      border-radius: _size(border-radius);
      border-bottom: 0;
      color: _palette(bg);
      text-align: center;
      text-decoration: none;
      z-index: 3;

      > :last-child {
        margin: 0;
      }

      h2 {
        margin: 0;
      }
    }

    &.planung {
      background-color: transparentize(_palette(fg-accent), 0.5);
    }

    &.bildung {
      background-color: transparentize(_palette(fg-accent), 0.5);
    }

    &.integration {
      background-color: transparentize(_palette(fg-accent), 0.5);
    }

    &.zivildienst {
      background-color: transparentize(_palette(fg-accent), 0.5);
    }

    &.prozess {
      background-color: _palette(fg-accent);

      a {
        color: _palette(bg);
      }
    }

    &.style6 {
      > :before {
        background-color: _palette(accent6);
      }
    }

    body:not(.is-touch) & {
      &:hover {
        @include vendor('transform', 'scale(1.025)');

        &:before {
          background-color: _palette(bg-accent);
          opacity: 0.35;
        }

        .icon {
          max-height: 15em;
          opacity: 1;
        }

        color: #ffffff !important;
      }
    }
  }

  * + & {
    margin-top: _size(element-margin);
  }

  body.is-preload & {
    article {
      @include vendor('transform', 'scale(0.9)');
      opacity: 0;
    }
  }

  body.is-touch & {
    article {
      .icon {
        max-height: 15em;
        opacity: 1;
      }
    }
  }

  @include breakpoint('<=medium') {
    $gutter: _size(gutter) * 0.5;

    margin: ($gutter * -1) 0 ($gutter * 1.5) ($gutter * -1);
    @include vendor('display', 'flex');

    article {
      width: calc(#{(100vw / 2)} - #{$gutter * 2.5});
      height: calc(#{(100vw / 2)} - #{$gutter * 2.5});
      margin: $gutter 0 0 $gutter;

      &:hover {
        @include vendor('transform', 'scale(1.0)');
      }
    }
  }

  @include breakpoint('<=small') {
    $gutter: _size(gutter) * 0.25;

    margin: ($gutter * -1) 0 ($gutter * 1.5) ($gutter * -1);

    article {
      width: calc(#{(100vw / 2)} - #{$gutter * 2.5});
      height: calc(#{(100vw / 2)} - #{$gutter * 2.5});
      margin: $gutter 0 0 $gutter;

      &:hover {
        @include vendor('transform', 'scale(1.0)');
      }
    }
  }

  @include breakpoint('<=xsmall') {
    $gutter: _size(gutter) * 0.25;

    margin: ($gutter * -1) 0 ($gutter * 2) ($gutter * -1);

    article {
      margin: $gutter 0 0 $gutter;

      .icon {
        display: none;
      }
    }
  }

  + .tiles {
    margin-top: 2em;
  }

}

