// Misc.
$misc: (
    z-index-base:		10000
);

// Duration.
$duration: (
    menu:				0.45s,
    transition:			0.2s
);

// Size.
$size: (
    border-radius:		4px,
    border-width:		1px,
    element-height:		3em,
    element-margin:		2em,
    gutter:				2.5em,
    field-gutter:		0.5em,
    inner:				68em,
    menu:				22em
);

// Font.
$font: (
    //family:				('Arial Narrow', Helvetica, Arial, sans-serif),
    family:             ('Roboto Condensed', sans-serif),
    family-fixed:		('Courier New', monospace),
    weight:				300,
    weight-bold:		900,
    weight-bold-alt:	700,
    letter-spacing:		0.25em,
    letter-spacing-alt:	-0.025em
);

// Palette.
$palette: (
    swo-grau:           #7A7C7E,
    bg:					#ffffff,
    bg-accent:			#7A7B7D,
    bg-alt:				#f6f6f6,
    fg:					#000000,
    fg-accent:			#005091,
    border:				#c9c9c9,
    border-bg:			rgba(144,144,144,0.075),
    accent1:			#134611,
    accent2:			#DB504A,
    accent3:			#FF6F59,
    accent4:			#A0CE4E,
    accent5:			#114664,
    accent6:			#8499e7,

    bildung:            #92BAAD,
    planung:            #bacfee,
    integration:        #eef2bc,
    zivildienst:        #dea5b1,
);